<script>
import firebase from "@/firebase/init.js"
export default {
  name: "Work",
  data: function() {
    return {
      showPlayer: false,
      src: "",
      work: []
    };
  },
  methods: {
    closePlayer() {
      this.src = "";
      this.showPlayer = false;
    },
    openPlayer(src) {
      this.showPlayer = true;
      this.src = src;
    }
  },
  created() {
    firebase.firestore().collection("work").doc("work").get()
    .then(doc => {
      this.work = doc.data();
    })
    .catch(err => {
    });
  }
};
</script>


